
.qy-chapter-tool{
	background:#f46094;
    position: fixed;
    bottom: 1.0rem;
    width: 100%;
}
.qy-add-bookrack{
	position: fixed;
	right:0;
	top:2rem;
	background:#f46094;
	color:#fff;
	border-radius: 1rem 0 0 1rem;
	padding:.2rem;
	font-size:.4rem;
}
.qy-chapter-setting{
	padding-top:.2rem;
}
.qy-setting-font{
	text-align: center;
	font-size:.6rem;
	color:#fff;
}
.qy-setting-font span{
	display: inline-block;
	width:1.6rem;
	height:.8rem;
	border:.02rem solid #fff;
	border-radius: .2rem;
}
.qy-setting-font span:nth-of-type(2){
	border-color:transparent;
}
.qy-setting-bg{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin:.2rem auto;
}
.qy-setting-bg li{
	width:15%;
	height:.5rem;
    line-height:.5rem;
    font-size:.4rem;
	background:#474d70;
    color: #f46094;
	text-align: center;
}
.qy-setting-bg li:nth-of-type(2){
	background: #6e6661;
}
.qy-setting-bg li:nth-of-type(3){
	background: #fae7e7;
}
.qy-setting-bg li:nth-of-type(4){
	background: #cdefe9;
}
.qy-bg-active:after{
	content:'\2714';
}



