
body{
	background: #fff;
}
.qy-read-wrap p{
	font-size:.4rem;
	line-height: 1.5em;
	padding-bottom: .2rem;
}
/*字体功能*/
.qy-fs-1 p{
	font-size:.3rem;
	line-height: 1.5em;
}
.qy-fs-2 p{
	font-size:.4rem;
	line-height: 1.5em;
}
.qy-fs-3 p{
	font-size:.5rem;
	line-height: 1.5em;
}
.qy-fs-4 p{
	font-size:.6rem;
	line-height: 1.5em;
}
.qy-fs-5 p{
	font-size:.7rem;
	line-height: 1.5em;
}
/*背景功能*/
.qy-bg-0 .yd-list-theme4,.qy-bg-0 .yd-list-mes,.qy-bg-0 .qy-chapter-name,.qy-bg-0 p{
	background:black;
	color:#fff;
}
.qy-bg-1 .yd-list-theme4,.qy-bg-1 .yd-list-mes,.qy-bg-1 .qy-chapter-name,.qy-bg-1 p{
	background:#474d70;
	color:#fff;
}
.qy-bg-2 .yd-list-theme4,.qy-bg-2 .yd-list-mes,.qy-bg-2 .qy-chapter-name,.qy-bg-2 p{
	background:#6e6661;
	color:#fff;
}
.qy-bg-3 .yd-list-theme4,.qy-bg-3 .yd-list-mes,.qy-bg-3 .qy-chapter-name,.qy-bg-3 p{
	background:#fae7e7;
}
.qy-bg-4 .yd-list-theme4,.qy-bg-4 .yd-list-mes,.qy-bg-4 .qy-chapter-name,.qy-bg-4 p{
	background:#cdefe9;
}



